
import XTable from 'vux/src/components/x-table/index.vue'

export default {
data() {
return {
topTitle: "收益记录",
DataList: {},
listTable: []
};
},
filters: {
formatDate: function(value) {
let val = value * 1000;
let date = new Date(val);
let y = date.getFullYear();
let MM = date.getMonth() + 1;
MM = MM < 10 ? "0" + MM : MM;
let d = date.getDate();
d = d < 10 ? "0" + d : d;
let h = date.getHours();
h = h < 10 ? "0" + h : h;
let m = date.getMinutes();
m = m < 10 ? "0" + m : m;
let s = date.getSeconds();
s = s < 10 ? "0" + s : s;
return y + "-" + MM + "-" + d + " " + h + ":" + m + ":" + s;
},
formatDates: function(value) {
let val = value * 1000;
let date = new Date(val);
let y = date.getFullYear();
let MM = date.getMonth() + 1;
MM = MM < 10 ? "0" + MM : MM;
let d = date.getDate();
d = d < 10 ? "0" + d : d;

return y + "-" + MM + "-" + d;
},
uppercase: function(value) {
if (!value) {
return "";
}
return value.toUpperCase();
}
},
beforeMount() {
this.getDataDetail();
this.getDatalist();
},
methods: {
getDataDetail() {
var that = this,
formData = {
sign: that.$store.state.user.sign,
access_token: that.$store.state.user.token,
order_id: that.$route.params.order_id
};
that
.$request({
method: "POST",
url:"/income/order-income-detail",
data: that.$qs.stringify(formData)
})
.then(function(response) {
if (response.data.yesterday_income == null) {
response.data.yesterday_income = 0.0;
}
if (response.data.provide_num == null) {
response.data.provide_num = 0.0;
}

that.DataList = response.data;
that.DataList.provide_num_rem = parseFloat((Number(response.data.provide_num) *Number(that.$store.state.user.bct_cnc)).toFixed(8));
that.DataList.yesterday_income_rem = parseFloat((Number(response.data.yesterday_income) *Number(that.$store.state.user.bct_cnc)).toFixed(8));
})
.catch(function(error) {console.log(error);});
},
getDatalist() {
var that = this,
formData = {
sign: that.$store.state.user.sign,
access_token: that.$store.state.user.token,
order_id: that.$route.params.order_id
};
that
.$request({
method: "POST",
url: that.$baseURL + "/income/order-income-record-list",
data: that.$qs.stringify(formData)
})
.then(function(response) {
that.listTable = response.data;
})
.catch(function(error) {
console.log(error);
});
}
},
components: {
XTable
}
};



